<template>
  <div class='cookpage'>
  	<div v-for="item in [detailArr]" class="detail">
  		<div class="head">
	  		<div class="header1">
	  			<span @click='goBackFn'>
	  				<img src="../../assets/img/back-w.png" alt="" v-show="blackBol" class="back1">
  					<img src="../../assets/img/back-l.png" alt="" v-show="!blackBol" class="back1">
	  			</span>
  				<span>{{mes}}</span>
		  		<ul class="nav" @click="HearNavFn">
		  			<li></li>
		  			<li></li>
		  			<li></li>
		  		</ul>
  			</div>
	  		<img :src="detailArr.albums" alt="">
	  	</div>
		<!-- 2 -->
	  	<div class="collection">
	  		<p class="text1">{{detailArr.title}}</p>
	  		<span>{{clect}}</span>
	  		<span>{{see}}</span>
	  	</div>
		<!-- 3 -->
	  	<div>
	  		<span></span>
	  		<div v-for='(star,index) in starArr'  class="star">
	  			<img :src="star.url1" alt="" v-show='starBol[index]'><img :src="star.url2" alt="" v-show='!starBol[index]'>
	  		</div>
	  		<div class="score"><img src="../../assets/img/score.png" alt=""></div>
	  		<ul>
	  			<li v-for='prac in practiceArr'><img :src="prac.src" alt="">{{prac.name}}</li>
	  		</ul>
	  	</div>
		<!-- 4 -->
	  	<div>
	  		<div></div>
	  		<div v-for ='ingre in ingredientsArr'>
	  			<span>{{ingre[0]}}</span>
	  			<span>
	  				<span>{{ingre[1]}}</span>
	  			</span>
	  		</div>
	  	</div>
		<!-- 5 -->
	  	<div>
	  		<div></div>
	  		<div v-for ='access in accessoriesArr'><span>{{access[0]}}</span><span>{{access[1]}}</span></div>
	  	</div>
	  	<!-- 6 -->
	  	<div>
	  		<ul>
	  			<li></li>
	  			<li><img src="../../assets/img/basket.png" alt=""></li>
	  			<li @click="listFn"><span>+</span></li>
	  		</ul>
	  	</div>
		<!-- 7 -->
	  	<div>
	  		<ul v-for="(step,index) in stepArr">
	  			<li>{{index}}</li>
	  			<li><img :src="step.img" alt=""></li>
	  			<li>{{step.step}}</li>
	  		</ul>
	  		<div>
	  			<p></p>
	  			<img :src="finishedArr[0]" alt="" class="FinishDrawing">
	  		</div>
	  		<div class="finish">
	  			<span v-for='(finish,index) in finishedArr' class="mask" @click="maskFn(index)"><img :src="finish" alt=""></span>
	  		</div>
	  	</div>
	  	<!-- 8 -->
	  	<div>
	  		<p></p>
	  		<p>{{experience}}</p>
	  		<div></div>
	  	</div>
	  	<!-- 9 -->
	  	<div>
	  		<p></p>
	  		<ul v-for="(comm,index) in commentArr" v-show="commBol[index]">
	  			<li>
  					<span>
  						<img :src="comm.src" alt="">
  					</span>
  					<span>
  						<span>{{comm.name}}</span>
  						<span>{{comm.time}}{{minue[0]}}</span>
  					</span>
	  			</li>
	  			<li><p>{{comm.txt}}</p></li>
	  		</ul>
	  		<div>
	  			<span @click='comMoreFn'>{{comMore[n]}}({{commNum}})</span>
	  			<span @click="commentFn"></span>
	  		</div>
	  		<div class="stop" @click="toTopFn"></div>
	  	</div>
	  	<!-- 10 -->
	  	<div>
	  		<span v-for="(bot,ind) in bottomArr" @click="navFn(ind)"><img :src='bot.url1' alt="" class="navClass"></span>
	  	</div>
	  	<div class="func" v-show="HearNavBol">
  			<span class="triangle"></span>
  			<ul>
  				<li v-for="(fn,index) in funcArr">{{fn}}</li>
  			</ul>
  		</div>
  		<div class="suspen">
          <p></p>
          <a href="###" v-for='(item,index) in suspenArr' @click='friendShareFn(index)'><img v-bind:src="item.url" alt="">{{item.txt}}</a>
        </div>
  	</div>
  	

  </div>
</template>

<script>
import {mapGetters,mapActions} from "vuex"
import { Toast } from 'mint-ui'
export default {
  name: '',
  data () {
    return {
    	HearNavBol:false,
    	funcArr:["","","",""],
     	mes:"",
     	clect:2809,
     	see:17022,
     	starArr:[
     		{url1:require("../../assets/img/star.png"),url2:require("../../assets/img/star-a.png")},
     		{url1:require("../../assets/img/star.png"),url2:require("../../assets/img/star-a.png")},
     		{url1:require("../../assets/img/star.png"),url2:require("../../assets/img/star-a.png")},
     		{url1:require("../../assets/img/star.png"),url2:require("../../assets/img/star-a.png")},
     		{url1:require("../../assets/img/star.png"),url2:require("../../assets/img/star-a.png")}
     	],
     	starBol:[false,false,true,true,true],
     	practiceArr:[
     		{src:require("../../assets/img/cook_way.png"),name:""},
     		{src:require("../../assets/img/seasoning.png"),name:""},
     		{src:require("../../assets/img/clock.png"),name:"<60"},
     		{src:require("../../assets/img/fire.png"),name:""}
     	],
     	ingredientsArr:[],
     	accessoriesArr:[],
     	stepArr:[],
     	finishedArr:[],
     	experience:"",
     	commNum:0,
     	blackBol:true,
     	minue:["",""],
     	commBol:[],
     	comMore:["",""],
     	n:0,
     	comMoreBol:true,
     	selectBol:true,
     	bottomArr:[
     		{url1:require("../../assets/img/comment.png"),url2:require("../../assets/img/comment-a.png")},
     		{url1:require("../../assets/img/store.png"),url2:require("../../assets/img/store-a.png")},
     		{url1:require("../../assets/img/send.png"),url2:require("../../assets/img/send_b.png")}
     	],
     	suspenArr:[
	        {url:require("../../assets/img/QQ.png"),txt:"QQ"},
	        {url:require("../../assets/img/wx.png"),txt:""},
	        {url:require("../../assets/img/wb.png"),txt:""},
	        {url:require("../../assets/img/friend.png"),txt:""}
	    ],
	    shareBol:true
    }
  },
  //
  computed:mapGetters({
  	commentArr:'getCommentArr',
  	detailArr:"getDetailArr"
  }),
  methods:{
  	//
  	HearNavFn:function(){
		if (this.HearNavBol==false) {
			$(".func").slideDown();
		}else {
			$(".func").slideUp();
		}
		this.HearNavBol=!this.HearNavBol;  
  	},
  	//
  	goBackFn:function(){
  		this.$router.push("/")
  		var that=this;
  		this.removeEvent(window,'scroll',that.scrollFn)
  	},
  	listFn:function(){
  		this.$router.push("/list")
  		var that=this;
  		this.removeEvent(window,'scroll',that.scrollFn)
  	},
  	maskFn:function(index){
  		$(".mask").removeClass("mask1");
  		$(".FinishDrawing").get(0).src=this.finishedArr[index];
  		$(".mask").eq(index).addClass("mask1");
  	},
  	//
  	commentFn:function(){
  		this.$router.push("/comment")
  		var that=this;
  		this.removeEvent(window,'scroll',that.scrollFn)
  	},
  	seeTwoFn:function(){
  		this.commBol=[];
  		for(var i=2;i<this.commentArr.length-2;i++){
	  		this.commBol.push(false)
	  	}
	  	this.commBol[0]=true;
	  	this.commBol[1]=true;
  	},
  	//
  	comMoreFn:function(){
  		if (this.comMoreBol) {
  			var comArr=[];
	  		for(var i=0;i<this.commentArr.length;i++){
	  			comArr.push(true);
	  		}
	  		this.commBol=comArr;
	  		this.n = 1;
  		}else {
  			this.seeTwoFn();
  			this.n = 0;
  		}
  		this.comMoreBol=!this.comMoreBol;
  	},
  	//
  	toTopFn:function(){
  		// scrollTo(0,0);
  		$('html,body').animate({scrollTop: '0px'},500)
  		
  	},
  	navFn:function(ind){
  		var that = this;
  		switch (ind) {
  			case 0:that.commentFn();break;
  			case 1:
  				if (this.selectBol) {
  					Toast({message: "",duration:1000});
  					$(".navClass").eq(1).get(0).src = that.bottomArr[1].url2;
  				}else {
  					Toast({message: "",duration:1000});
  					$(".navClass").eq(1).get(0).src = that.bottomArr[1].url1;
  				}
  					this.selectBol=!this.selectBol;break;
  			case 2:
  			if (this.shareBol) {
  				$(".suspen").stop().animate({right:0},800);
  				$(".navClass").eq(2).get(0).src = that.bottomArr[2].url2;
  			}else {
  				$(".suspen").stop().animate({right:'-510px'},1000);
  				$(".navClass").eq(2).get(0).src = that.bottomArr[2].url1;
  			}
  				this.shareBol=!this.shareBol;break;
  			default:break;
  		}
  	},
  	friendShareFn:function(index){
      $(".suspen").stop().animate({right:'-510px'},1000);
      this.shareBol=false;
      var value = this.suspenArr[index].txt;
      Toast({
        message: ""+value,
        iconClass: 'icon icon-success',
        duration:1300
      });
    },
  	//
  	jsonToArr:function(obj){
  		var a = obj.split(';');
		var json = {};
		var arr = [];
		for(var i=0;i<a.length;i++){
		    json[i]=a[i];
		    arr.push(json[i].split(','))
		}
		return arr;
	},
	scrollFn:function(){
		var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
  		var end=$(".collection").get(0).offsetTop+$(".collection").get(0).offsetHeight-$(".header1").get(0).offsetHeight;
  		var mes = (parseFloat(scrollTop/1000)+0.2).toFixed(2);
  		var tet = $(".text1").get(0).getBoundingClientRect().top;
  		if (scrollTop==0) {
  			$(".header1").css({"backgroundColor":"rgba(255,255,255,0)","borderBottomColor":"transparent"});
  			this.mes="";
  		}else if(scrollTop>0&&scrollTop<300){
  			$(".header1").css({"backgroundColor":"rgba(255,255,255,"+mes+")","display":"block"});
  			this.blackBol=true;
  			$(".nav>li").css({"backgroundColor":"#fff","borderBottomColor":"transparent"})
  			this.mes="";
  		}else if (scrollTop>=300&&scrollTop<end) {
  			$(".header1").css({"backgroundColor":"rgba(255,255,255,"+mes+")","display":"block"});
  			$(".header1").stop().animate({"height":"1.5333rem"},400)
  			this.blackBol=false;
  			$(".nav>li").css({"backgroundColor":"black","borderBottomColor":"transparent"})
  			this.mes="";
  		}else if (tet<$(".header1").get(0).offsetHeight) {
  			this.mes=this.detailArr.title
  			$(".header1").css({"backgroundColor":"rgba(255,255,255,1)","borderBottomColor":"#ccc"});
  		}else {
  			$(".header1").css({"backgroundColor":"rgba(255,255,255,1)","borderBottomColor":"#ccc"});
  			this.mes="";
  		}
	},
	addEvent:function(element,type,handle){
    	//addEventListener
		if(window.addEventListener){
		  	element.addEventListener(type,handle,false)
		}
		//attchEvent
		else if (window.attachEvent) {
	  		element.attachEvent("on"+type,handle);
		}else {
		  	element["on"+type]=handle;
		}
	},
	removeEvent:function(element,type,handle){
	    //addEventListener
	    if(window.removeEventListener){
	      element.removeEventListener(type,handle,false)
	    }
	    //attchEvent
	    else if (window.attachEvent) {
	      element.attachEvent("on"+type,handle);
	    }else {
	      element["on"+type]=handle;
	    }
  	}

  },
  mounted:function(){
  	this.commNum = this.commentArr.length;
  	$(".mask").eq(0).addClass("mask1");
  	var that = this;
  	//
  	this.addEvent(window,'scroll',that.scrollFn); 
  	this.seeTwoFn();
	this.ingredientsArr=this.jsonToArr(this.detailArr.ingredients);
	this.accessoriesArr=this.jsonToArr(this.detailArr.burden);
	this.stepArr=this.detailArr.steps
	var finishArr = [];
	for(var i=0;i<this.stepArr.length;i++){
		finishArr.unshift(this.stepArr[i].img)
	}
	finishArr.length=4;
	this.finishedArr=finishArr;

  }
}
</script>

